'use client';

import { create } from 'zustand';

export type SensorData = {
  temperature: number;
  humidity: number;
  waterLevel: number;
};

type BluetoothState = {
  isConnected: boolean;
  deviceName: string | null;
  sensorData: SensorData;
  connect: (device: BluetoothDevice, onData: (data: SensorData) => void) => Promise<void>;
  disconnect: () => void;
  setSensorData: (data: Partial<SensorData>) => void;
};

export const useBluetoothStore = create<BluetoothState>((set, get) => ({
  isConnected: false,
  deviceName: null,
  sensorData: {
    temperature: 0,
    humidity: 0,
    waterLevel: 0,
  },
  connect: async (device, onData) => {
    set({ deviceName: device.name, isConnected: true });
    // This is where you would handle the GATT connection and characteristic notifications
  },
  disconnect: () => {
    // This is where you would handle disconnection logic
    set({ isConnected: false, deviceName: null });
  },
  setSensorData: (data) => {
    set(state => ({ sensorData: { ...state.sensorData, ...data }}));
  },
}));
